.Block-ListSpecificPositions {
    padding: 0;
    margin: 0;
    width: 100%;
  }
  .Block-SpecificPosition {
    display: flex;
    position: relative;
  }
  
  .Block-SpecificPosition__content {
    max-width: 65%;
  }
  .Block-SpecificPosition__name {
    text-overflow: ellipsis;
    overflow: hidden;
  }
  .Block-SpecificPosition__description {
    display: flex;
  }
  
  .Block-SpecificPosition__positionOrder {
    color: #b4b7bd;
    font-size: 12px;
    line-height: 14px;
    padding-top: 5px;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .Block-SpecificPosition__amount {
    position: absolute;
    right: 5px;
    top: 20%;
  
    color: #413dff;
    font-size: 16px;
    font-weight: 500;
    line-height: 20px;
  
    max-width: 40%;
  }
  .Block-SpecificPosition__label {
    display: flex;
    justify-content: baseline;
    color: #80869a;
    font-size: 16px;
    font-weight: 400;
    line-height: 16px;
    margin-bottom: 8px;
  
  }
  
  .Block-SpecificPosition__option {
  
    margin-bottom: 0.25em;

    position: relative;
  }
  
  .Block-SpecificPosition__checkInput {
  
    position: absolute;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
  
  }
  
  .Block-SpecificPosition___FakeCheckbox {
  
    display: inline-block;
    position: relative;
    cursor: pointer;
    height: 14px;
    min-width: 14px;
  
    margin-right: 1.2em;
    border-radius: 0.25em;
    border: 1.5px solid #dcdee3;
    background-color: white;

    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  
  }
  
  .Block-SpecificPosition___FakeCheckbox::before {
  
    content: "";
    position: absolute;
    width: 5px;
    height: 10px;
    border-style: solid;
    border-width: 0 2px 2px 0;
    transform: rotate(45deg);
    border-color: #413dff;
    display: block;
    left: 3px;
    opacity: 0;
    transition: .2s;
  
  }
  
  .Block-SpecificPosition__checkInput:checked + .Block-SpecificPosition___FakeCheckbox::before {
  
    opacity: 1;
  }
  