
.check__input {

  position: absolute;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;

}

.check_box {

  display: inline-block;
  position: relative;
  cursor: pointer;
  height: 14px;
  min-width: 14px;

  margin-right: 1.2em;
  border-radius: 0.25em;
  border: 1.5px solid #dcdee3;
  background-color: white;

}

.check_box::before {

  content: "";
  position: absolute;
  width: 5px;
  height: 10px;
  border-style: solid;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
  border-color: #413dff;
  display: block;
  left: 3px;
  opacity: 0;
  transition: .2s;

}
.check__input:checked+.check_box::before {

  opacity: 1;
}


